'use client';

import { useState, useTransition } from 'react';
import { Button } from '@/components/ui/button';
import { ThumbsUp, Loader2 } from 'lucide-react';
import { submitVote } from '@/app/actions';
import { useToast } from '@/hooks/use-toast';
import type { User } from '@/types';

interface VoteButtonProps {
  buildingId: string;
  currentUser: User | null;
  initialVotes: number; // Though this might become stale, server action will revalidate
}

export default function VoteButton({ buildingId, currentUser, initialVotes }: VoteButtonProps) {
  const [isPending, startTransition] = useTransition();
  const { toast } = useToast();
  // Note: Optimistic updates for votes are complex with revalidatePath. 
  // The server action revalidates, so the UI will update after server confirmation.
  // For immediate feedback, we rely on the loading state.

  const handleVote = async () => {
    if (!currentUser) {
      toast({
        title: '请先登录',
        description: '登录后才能为作品投票。',
        variant: 'default',
        duration: 3000,
      });
      // Optionally, redirect to login: router.push('/login');
      return;
    }

    startTransition(async () => {
      const result = await submitVote(buildingId);
      if (result.success) {
        toast({
          title: '投票成功！',
          description: result.message,
          variant: 'default',
        });
      } else {
        toast({
          title: '投票失败',
          description: result.message,
          variant: 'destructive',
        });
      }
    });
  };

  return (
    <Button 
      onClick={handleVote} 
      disabled={isPending || !currentUser} 
      variant="outline" 
      className="border-accent text-accent hover:bg-accent hover:text-accent-foreground"
      aria-label={`为 ${buildingId} 投票`}
    >
      {isPending ? (
        <Loader2 className="mr-2 h-4 w-4 animate-spin" />
      ) : (
        <ThumbsUp className="mr-2 h-4 w-4" />
      )}
      {isPending ? '投票中...' : '投票'}
    </Button>
  );
}
